body {
  margin: 0;
}

.App {
  text-align: center;
}

.btn {
  cursor: pointer;
}

.black-nav {
  display: flex;
  background-color: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

div {
  box-sizing: border-box;
}

.list {
  padding: 0 20px 0 20px;
  text-align: left;
  border-bottom: 1px solid grey;
}

.modal {
  margin: 20px 20px 20px 20px;
  padding: 20px;
  background: #eee;
  text-align: center;
}

.input-field::placeholder {
  color: #dadada;
}
